.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -webkit-transition: opacity 250ms ease-in-out;
  -o-transition: opacity 250ms ease-in-out;
  transition: opacity 250ms ease-in-out; }

*:focus {
  outline: none; }

html, body {
  height: 100%;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: #5a5a5a; }

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

img {
  max-width: 100%;
  display: block; }

.clear {
  clear: both; }

input[type=number] {
  border: none;
  -moz-appearance: textfield; }
  input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }

a {
  color: #5a5a5a;
  text-decoration: none; }
  a:hover {
    opacity: .9; }

.btn {
  background: #2b5b78;
  color: #f3f3f3;
  padding: 10px 50px;
  font-size: 20px;
  line-height: 1.3;
  text-decoration: none;
  border-radius: 10px; }

.container {
  max-width: 738px;
  margin: 0 auto;
  padding: 10px 15px; }
  .container #beach-number {
    float: left; }
  .container #language-switcher {
    float: right; }
    .container #language-switcher select {
      border: none;
      color: #5a5a5a; }
  .container #search-wrap {
    position: relative; }
    .container #search-wrap::after {
      content: '';
      position: absolute;
      left: -15px;
      bottom: 0;
      width: calc(100% + 30px);
      height: 1px;
      background: #e4e4e5; }
    .container #search-wrap #search-button {
      position: absolute;
      width: 20px;
      height: 20px;
      left: 7px;
      top: 7px;
      cursor: pointer; }
      .container #search-wrap #search-button:hover {
        opacity: .9; }
    .container #search-wrap #search {
      width: 100%;
      background: #e4e4e5;
      border: none;
      border-radius: 5px;
      padding: 10px 30px;
      color: #5a5a5a;
      margin-bottom: 10px; }
    .container #search-wrap #voice-search {
      position: absolute;
      width: 20px;
      height: 20px;
      right: 7px;
      top: 7px;
      cursor: pointer; }
      .container #search-wrap #voice-search:hover {
        opacity: .9; }
  .container #categories {
    margin: 30px 0 20px;
    display: flex;
    width: 100%;
    justify-content: space-between;
  }

    .container #categories ul {
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;
      flex-direction: row;
    }

      .container #categories ul li {
        display: flex;
        text-align: center;
        color: #2b5b78;
        cursor: pointer; }
        .container #categories ul li a {
          color: #2b5b78; }
  .container h1 {
    font-size: 22px;
    margin: 0 0 10px;
    color: #000;
    font-weight: bold; }
  .container h2 {
    font-size: 18px;
    margin: 0 0 10px;
    color: #000;
    font-weight: bold; }
  .container #total, .container #order-button {
    background: #2b5b78;
    color: #f3f3f3;
    width: 150px;
    text-align: center;
    border-radius: 10px;
    padding: 10px;
    margin: 30px auto;
    font-size: 20px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer; }
  .container #order-button {
    padding: 30px 10px; }
  .container #disclaimer {
    font-size: 14px; }
  .container #return {
    text-align: center;
    margin: 50px 0 20px; }

header #digiserve-logo {
  float: right;
  max-width: 150px;
  margin-bottom: 30px; }

footer {
  text-align: center;
  margin-top: -150px;
  font-size: 12px; 
  text-align: center;
}

.home .container h1, .confirmation .container h1 {
  text-align: center;
  font-size: 16px;
  font-weight: normal; }

.home .container #digiserve-logo, .confirmation .container #digiserve-logo {
  margin: 50px 0 30px;
  text-align: center; }
  .home .container #digiserve-logo img, .confirmation .container #digiserve-logo img {
    max-width: 150px;
    display: inline-block; }

.home .container #menu-btn, .confirmation .container #menu-btn {
  text-align: center; }

.details #content {
  max-width: 80%; }
  .details #content #details {
    margin-bottom: 40px; }
    .details #content #details input {
      float: left;
      width: 48%;
      margin-right: 4%;
      background: #e4e4e5;
      border: none;
      padding: 10px 15px;
      border-radius: 3px;
      margin-bottom: 10px;
      color: #5a5a5a; }
      .details #content #details input:first-of-type {
        width: 100%;
        margin-right: 0; }
      .details #content #details input:last-of-type {
        margin-right: 0; }
  .details #content #payment-method #payment-buttons {
    text-align: center; }
    .details #content #payment-method #payment-buttons .payment-button {
      padding: 20px;
      background: #e4e4e5;
      color: #2b5b78;
      border-radius: 5px;
      margin-bottom: 10px;
      cursor: pointer; }

      .details #content #payment-method #payment-buttons .payment-button.selected  {
        background: #2b5b78;
        color: white;
      }

      .details #content #payment-method #payment-buttons .payment-button:hover {
        opacity: .9; }

        .error {
          color: #c54646
        }

.confirmation #confirmation-message {
  text-align: center;
  max-width: 60%;
  margin: 0 auto; }

  .digiserve-logo {
    display: flex;
    justify-content: center;
  }

  .footer {
    margin-top: auto;
    padding: 20px;
    align-self: center;
  }