
  .container .categories {
    margin: 30px 0 20px;
    display: flex;
    width: 100%;
    justify-content: flex-start;
  }

  .container .categories a {
    padding: 0 15px;
    color: #5a5a5a;
    cursor: pointer;
  }

  .container .categories a.active {
    color: #2b5b78;
  }
  
  .menuItem.hidden {
    display: none;
  }
  .container .product-row {
    display: flex;
    width: 100%;
    flex-direction: row;
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #e4e4e5; 
    justify-content: flex-start;
    flex-wrap: wrap;
  }

    .container .product-row .product-info {
   }
      .container .product-row .product-info .product-price, .container .product-row .product-info .product-description {
        font-size: 12px; }

        .container .product-row .product-comment {
          flex-basis: 100%;
        }
      .container .product-row .product-comment input {
        padding: 5px 10px;
        border: 1px solid #d4d4d4;
        border-radius: 5px;
        flex-basis: 100%;
        color: #5a5a5a;
        margin-top: 10px;
       }
    .container .product-row .product-quantity {
      display: flex;
      flex-direction: row;
      text-align: center;
      margin-left: auto;
      align-items: center;
    }
      .container .product-row .product-quantity .quantity {
        display: flex;
        text-align: center;
        font-size: 20px;
        color: #5a5a5a; }

      .container .product-row .product-quantity .change-quantity {
        padding-left: 20px;
        display: flex;
       }
        .container .product-row .product-quantity .change-quantity span {
          display: flex;
          color: #2b5b78;
          padding: 0 30px;
          font-size: 30px;
          line-height: 25px;
          cursor: pointer;
          -webkit-touch-callout: none;
          -webkit-user-select: none;
          -khtml-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none; }
